
























































































import { Component, Vue } from "vue-property-decorator";
import Pagination from "../../components/pagination/pagination.vue";
import { apiUrl } from "../../api";
import { EditAuth } from "../../mixins";
@Component({
    name: "gutsbook",
    components: {
        Pagination
    }
})
export default class TimeLine extends EditAuth {
    private page = 1;
    private pageSize = 15;
    private count = 1;
    private tableData = [];
    private multipleSelection = [];
    private diaFlag = false;
    private dialogVisible = false;
    private ruleForm = {
        reply: "",
        id: ""
    };
    private rules = {
        reply: [
            { required: true, message: "请输入回复内容", trigger: "blur" },
            {
                min: 3,
                max: 200,
                message: "长度在 3 到 200 个字符",
                trigger: "blur"
            }
        ]
    };
    private handleSelectionChange(val: any) {
        this.multipleSelection = val;
    }
    created() {
        this.getData();
    }
    handleClose() {
        this.dialogVisible = false;
    }
    private getData() {
        (this as any).$axios
            .get(
                `${apiUrl.gutsbppkGet}?page=${this.page}&pageSize=${this.pageSize}`
            )
            .then((res: any) => {
                console.log(res);
                let datas = res.data;
                if (datas.code == 0) {
                    this.tableData = datas.message.rows;
                    this.count = datas.message.count;
                }
            });
    }
    private handleEdit(index: number, row: any) {
        this.dialogVisible = true;
        this.ruleForm.id = row.id;
        this.ruleForm.reply = row.reply;
    }
    private handleDelete(index: number, row: any) {
        this.$confirm("是否删除此条数据？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
        }).then(() => {
            this.diaFlag = true;
            this.deleteItem(row.id);
            this.getData();
        });
    }
    private deleteItem(id: number) {
        (this as any).$axios
            .get(`${apiUrl.gutsbppkDelete}?id=${id}`)
            .then((res: any) => {
                if (res.data.code == 0) {
                    if (this.diaFlag) {
                        this.$message.success("删除成功");
                        this.diaFlag = false;
                    }
                }
            });
    }
    setPage(page: number) {
        this.page = page;
        this.getData();
    }
    setPageSize(pageSize: number) {
        this.pageSize = pageSize;
        this.getData();
    }
    changeState(id: number) {
        if (!this.editFlag) {
            this.$message.warning("权限不足,请联系管理员");
            return false;
        }
        this.diaFlag = true;
        this.adopt(id);
    }
    private adopt(id: number) {
        (this as any).$axios
            .get(`${apiUrl.gustbookState}?id=${id}`)
            .then((res: any) => {
                if (res.data.code == 0 && this.diaFlag) {
                    this.$message.success(res.data.message);
                    this.diaFlag = false;
                    this.getData();
                }
            });
    }
    private removeAll() {
        let arr = this.multipleSelection;
        if (arr.length <= 0) {
            this.$message.warning("请选择数据");
            return false;
        }
        this.$confirm("是否批量删除数据？", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
        }).then(() => {
            arr.forEach((item: any, index: number) => {
                if (index >= arr.length - 1) {
                    this.diaFlag = true;
                }
                this.deleteItem(item.id);
            });
        });
    }
    private tong() {
        let arr = this.multipleSelection;
        if (arr.length <= 0) {
            this.$message.warning("请选择数据");
            return false;
        }

        arr.forEach((item: any, index: number) => {
            if (index >= arr.length - 1) {
                this.diaFlag = true;
            }
            if (!item.state) {
                this.adopt(item.id);
            }
        });
    }
    private submitForm(formName: string) {
        (this.$refs[formName] as any).validate((valid: boolean) => {
            if (valid) {
                this.relyadd();
            } else {
                console.log("error submit!!");
                return false;
            }
        });
    }
    relyadd() {
        (this as any).$axios
            .post(`${apiUrl.gustbookReply}`, this.ruleForm)
            .then((res: any) => {
                if (res.data.code === 0) {
                    this.$message.success("回复成功");
                    this.dialogVisible = false;
                    this.ruleForm = { reply: "", id: "" };
                    this.getData();
                }
            });
    }
}
